.div-home {
  max-width: 100vw;
  max-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  background-color: black;
  color: white;
}

.my-foto {
  background-image: url("../../assets/pictures/melad.jpeg");
  background-size: 300px;
  background-repeat: no-repeat;
  position: absolute;
  right: 0px;
  width: 30%;
  height: 100vh;
}

.wrapper-home {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100vw;
  min-height: 100vh;
  margin-top: 100px;
}

.wrapper-home h1 {
  margin: 30px;
  margin-top: 1px;
  font-size: x-large;
  font-weight: bold;
}

.wrapper-home p {
  margin: 10px 22px;
}
.wrapper-home h3,
.about-link {
  margin: 10px 30px;
}

.about-link {
  color: rgb(250, 198, 138);
  list-style: none;
  text-decoration: none;
  transition: 1s;
}

.about-link:hover {
  color: rgb(250, 227, 201);
  transform: scale(1.2);
}
