.Header-nav {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 35px;
  background-color: gray;
}

.header-nav-ul {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
  height: 100%;
  list-style: none;
  margin: 0;
}

.header-nav-link {
  color: black;
  padding: 10px;
  text-decoration: none;
  font-weight: bold;
  
}

.header-nav-link:hover {
  color: white;
}
